<template>
  <ul class="catalog__pagination pagination">
      <li class="pagination__item">
        <a class="pagination__link pagination__link--arrow"
          :class="{'pagination__link--disabled' : page <= 1}"
          @click.prevent="paginate(page - 1)"
          aria-label="Предыдущая страница">
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-left"></use>
          </svg>
        </a>
      </li>
      <li class="pagination__item"
          v-for="pageNum in countPages"
          :key="pageNum">
        <a href="#" class="pagination__link"
        :class="{ 'pagination__link--current' : pageNum === page }"
        @click.prevent="paginate(pageNum)">
          {{ pageNum }}
        </a>
      </li>
      <li class="pagination__item">
        <a class="pagination__link pagination__link--arrow"
        href="#" aria-label="Следующая страница"
        :class="{'pagination__link--disabled' : page >= countPages}"
        @click.prevent="paginate(page + 1)">
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-right"></use>
          </svg>
        </a>
      </li>
    </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: [
    'page',
    'itemsPerPage',
    'sumItems',
  ],
  computed: {
    countPages() {
      return Math.ceil(this.sumItems / this.itemsPerPage);
    },
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
  },
};
</script>
