<template>
  <div v-if="isProductLoading">Loading...</div>
  <div v-else-if="!productData">Something went wrong</div>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <RouterLink class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="#" @click.prevent="$router.push({name: 'main'})">
            {{ categoryInfo.title }}
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ productInfo.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img
            width="570"
            height="570"
            :src="productInfo.img"
            :alt="productInfo.title">
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ productInfo.id }}</span>
        <h2 class="item__title">
          {{ productInfo.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{ productInfo.price | numberFormat }} &#8381;
            </b>

            <fieldset class="form__block">
              <legend class="form__legend">Цвет:</legend>
              <ul class="colors">
                <li class="colors__item" v-for="(color, index) in colorInfo" :key="index">
                  <label class="colors__label" for="#">
                    <input
                      class="colors__radio sr-only"
                      type="radio"
                      name="color-item"
                      value="blue"
                      checked="">
                    <span class="colors__value" :style="{backgroundColor: color}">
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <fieldset class="form__block">
              <legend class="form__legend">Объемб в ГБ:</legend>

              <ul class="sizes sizes--primery">
                <li class="sizes__item">
                  <label class="sizes__label" for="#">
                    <input
                      class="sizes__radio sr-only"
                      type="radio"
                      name="sizes-item"
                      value="32">
                    <span class="sizes__value">
                      32gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label" for="#">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="64">
                    <span class="sizes__value">
                      64gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label" for="#">
                    <input
                      class="sizes__radio sr-only"
                      type="radio"
                      name="sizes-item"
                      value="128"
                      checked="">
                    <span class="sizes__value">
                      128gb
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <div class="form__counter">
                <button
                  type="button"
                  aria-label="Убрать один товар"
                  @click.prevent="reduceProductAmount">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <label for="#">
                  <input type="text" v-model.number="productAmount">
                </label>

                <button
                  type="button"
                  aria-label="Добавить один товар"
                  @click.prevent="increaseProductAmount">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <button class="button button--primery" type="submit" :disabled="productPending">
                В корзину
              </button>
            </div>
            <div v-show="productAdded">Товар добавлен в корзину</div>
            <div v-show="productPending">Добавляем товар в корзину....</div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              Описание
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Характеристики
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Гарантия
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Оплата и доставка
            </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
            Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br>
            Синхронизация co смартфоном<br>
            Связь по Bluetooth Smart, ANT+ и Wi-Fi<br>
            Поддержка сторонних приложений<br>
          </p>

          <a href="#">
            Bce характеристики
          </a>

          <h3>Что это?</h3>

          <p>
            Wahoo ELEMNT BOLT GPS - это велокомпьютер, который позволяет
            оптимизировать свои велотренировки,
            сделав их максимально эффективными. Wahoo ELEMNT BOLT
            GPS синхронизируется c датчиками по ANT+,
            объединяя полученную c них информацию. Данные отображаются на дисплее,
            a также сохраняются на смартфоне.
            При этом на мобильное устройство можно установить как фирменное приложение,
            так и различные приложения сторонних разработчиков.
            Велокомпьютер точно отслеживает местоположение,
            принимая сигнал c целого комплекса спутников.
            Эта информация позволяет смотреть уже преодоленные
            маршруты и планировать новые велопрогулки.
          </p>

          <h3>Дизайн</h3>

          <p>
            Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры устройства составляют
            всего 74,6 x 47,3 x 22,1 мм.
            что не превышает габариты смартфона. Корпус гаджета выполнен из черного пластика.
            Ha обращенной к пользователю стороне расположен дисплей диагональю 56 мм.
            co дисплей выводятся координаты и скорость, a также полученная co смартфона
            и синхронизированных
            датчиков информация: интенсивность, скорость вращения педалей, пульс и т.д.
            (датчики не входят в комплект поставки). Корпус
            велокомпьютера имеет степень защиты от влаги IPX7.
            Это означает, что устройство не боится пыли, a
            также выдерживает кратковременное (до 30 минут)
            погружение в воду на глубину не более 1 метра.
          </p>
        </div>
      </div>
    </section>
  </main>
</template>
<script>
import goToPage from '@/helpers/goToPage';
import numberFormat from '@/helpers/numberFormat';
import { RouterLink } from 'vue-router';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      productAmount: 1,
      productData: null,
      isProductLoading: false,

      productAdded: false,
      productPending: false,
    };
  },
  filters: {
    numberFormat,
  },
  methods: {
    ...mapActions(['addProductToCart']),
    goToPage,
    addToCart() {
      this.productAdded = false;
      this.productPending = true;
      this.addProductToCart({
        productId: this.productInfo.id,
        amount: this.productAmount,
      })
        .then(() => {
          this.productAdded = true;
          this.productPending = false;
        });
    },
    reduceProductAmount() {
      --this.productAmount;
    },
    increaseProductAmount() {
      ++this.productAmount;
    },
    loadProduct() {
      this.isProductLoading = true;
      axios.get(`${API_BASE_URL}api/products/${this.$route.params.id}`)
        .then((response) => this.productData = response.data)
        .finally(() => this.isProductLoading = false);
    },
  },
  computed: {
    productInfo() {
      return this.productData;
    },
    categoryInfo() {
      return this.productData.category;
    },
    colorInfo() {
      return this.productInfo.color;
    },
  },
  components: {
    RouterLink,
  },
  created() {
    this.loadProduct();
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>
