<template>
  <li class="cart__item product" :key="cartItem.productId">
    <div class="product__pic">
      <img :src="cartItem.product.img" width="120" height="120" alt="cartItem.product.title">
    </div>
    <h3 class="product__title">
      {{ cartItem.product.title }}
    </h3>
    <p class="product__info">
      Объем: <span>128GB</span>
    </p>
    <span class="product__code">
      {{ cartItem.product.productId }}
    </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <label for="#">
        <input type="text" v-model.number="amount" name="count">
      </label>

      <button type="button" aria-label="Добавить один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (cartItem.amount * cartItem.product.price) | numberFormat }} &#8381;
    </b>

    <button class="product__del button-del" type="button"
            @click.prevent="removeFromCart(cartItem.productId)"
            aria-label="Удалить товар из корзины">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>
<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';

export default {
  props: [
    'cartItem',
  ],
  filters: {
    numberFormat,
  },
  computed: {
    amount: {
      get() {
        return this.cartItem.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {
          productId: this.cartItem.productId,
          amount: value,
        });
      },
    },
  },
  methods: {
    ...mapActions({ removeProductFromCart: 'removeProductFromCart' }),
    removeFromCart(productId) {
      this.removeProductFromCart({
        productId,
      });
    },
  },
};
</script>
